/* Estilos globales */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Contenedor principal */
.App {
  text-align: center;
  background-image: url('./santander-logo2.png'); /* Ruta relativa en src */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 100vh;
}


/* Estilo del encabezado */
.App-header {
  background-color: #D50000; /* Rojo del Banco Santander */
  padding: 20px;
  color: white;
}

/* Título principal */
.App-header h1 {
  font-size: 2.5rem;
  margin: 0;
}

/* Descripción debajo del título */
.App-header p {
  font-size: 1.2rem;
  margin-top: 10px;
}

/* Contenedor de secciones */
.App-sections {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #F0F0F0; /* Gris claro */
}

/* Estilo de cada sección */
.App-section {
  width: 100%;
  max-width: 600px;
  margin-bottom: 20px;
  padding: 20px;
  background-color: #FFFFFF;
  border: 2px solid #D50000; /* Rojo del Banco Santander */
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center; /* Centra el texto dentro de la caja */
}

/* Títulos de las secciones */
.App-section h2 {
  font-size: 1.5rem;
  color: #333333; /* Gris oscuro */
  margin-bottom: 10px;
}

/* Estilo de los enlaces */
.App-link {
  color: #D50000; /* Rojo del Banco Santander */
  text-decoration: none;
  font-weight: bold;
}

.App-link:hover {
  text-decoration: underline;
}

/* Adaptación para pantallas pequeñas */
@media (max-width: 600px) {
  .App-section {
    width: 90%;
  }
}
